import { Decoration } from '@ali/4ever-cangjie';
import { MatchMark } from '../mo/marks';

const getMatchedDecorations = (decorations: Decoration[]) => {
  return decorations.filter((d) => MatchMark.isMatchMark(d.mark));
};

const getNoMatchedDecorations = (decorations: Decoration[]) => {
  return decorations.filter((d) => !MatchMark.isMatchMark(d.mark));
};

export {
  getMatchedDecorations,
  getNoMatchedDecorations,
};
